import React, { useEffect, useState } from "react";

const MovieList = (props) => {
  const [movies, setMovies] = useState([]);

  useEffect(() => {
    const url = `https://cms.dotcheckout.com/movies/`;
    fetch(url)
      .then((res) => res.json())
      .then((res) => setMovies(res));
  }, []);

  return (
    <>
      <table
        sx={{ minWidth: 650 }}
        aria-label="simple table"
        class="table table-dark table-striped"
      >
        <thead>
          <tr>
            <td>Name of the movie</td>
            <td>Number of Tickets</td>
            <td>Location</td>
            <td>Price per Tickets</td>
            <td>Show Timings</td>
            <td>Contact Number</td>
          </tr>
        </thead>
        <tbody>
          {movies.map((items) => {
            return (
              <tr key={items.id}>
                <td>{items.movie_name}</td>
                <td>{items.no_of_tickets}</td>
                <td>{items.theatre_location}</td>
                <td>{items.price_per_ticket}</td>
                <td>{`${items.show_date},${items.show_time}`}</td>
                <td>{items.listed_by_contact}</td>
              </tr>
            );
          })}
        </tbody>
        ;
      </table>
    </>
  );
};

export default MovieList;
